.slider {
  display: flex;
  flex-direction: column;
  justify-content: center;
  height: 100%; }

.rail {
  display: flex;
  position: relative;
  align-items: center;
  background: white;
  height: 6px;
  border: 1px solid rgba(31, 32, 65, 0.25);
  border-radius: 3px;
  box-sizing: border-box;
  cursor: pointer; }

.thumb {
  display: flex;
  z-index: 10;
  position: absolute;
  margin-left: -8px;
  margin-top: -6px;
  background: linear-gradient(180deg, #6FCF97 0%, #66D2EA 100%);
  border: 2px solid white;
  border-radius: 10px;
  padding: 6px;

  &__tooltip {
    position: absolute;
    z-index: 10;
    display: flex;
    align-items: center;
    justify-content: center;
    width: 30px;
    height: 20px;
    top: -30px;
    left: -10px;
    border: 2px solid #66D2EA;
    background-color: #6FCF97;
    opacity: 0.5;
    border-radius: 5px; } }

.progress {
  position: absolute;
  background: linear-gradient(180deg, #6FCF97 0%, #66D2EA 100%);
  border-radius: 3px;
  height: 100%; }

.scale {
  display: block;
  margin-top: 5px;
  cursor: pointer;

  &__wrapper {
    display: flex;
    justify-content: space-between; }

  &__division {
    display: flex;
    justify-content: space-around;
    align-items: flex-end;
    width: 100%;
    height: 8px;
    border-bottom: 2px solid #66D2EA;
    border-right: 2px solid #66D2EA;
    padding-bottom: 2px;

    &:first-child {
      border-left: 2px solid #66D2EA; } }

  &__subdivision {
    width: 100%;
    height: 5px;

    &:not(:first-child) {
      border-left: 1px solid #66D2EA; } }

  &__values {
    display: flex;
    justify-content: space-between; }

  &__valuesItem {
    display: flex;
    justify-content: center;
    width: 0;
    color: #6FCF97; } }
